.main-container {
  width: 768px;
  text-align: center;
  border-radius: 4px;
  background-color: rgb(255, 255, 255);
  padding-bottom: 20px;
}

.title {
  background-color: rgb(0, 106, 166);
  border-radius: 4px 4px 0 0;
  padding: 10px 0;
  color: white;
}

.content {
  color: rgb(0, 0, 0);
  padding: 5px;
}

.button-section {
  margin: 10px;
}

.update-time {
  font-size: 0.8rem;
  margin-bottom: 10px;
  color: rgba(0, 0, 0, 0.6);
}

footer {
  color: rgba(0, 0, 0, 0.6);
  font-weight: 500;
}

button {
  outline: none;
  user-select: none;
  border: none;
  text-decoration: none;
  font-weight: 500;
  cursor: pointer;
  background-color: #3397cf;
  color: white;
  border-radius: 8px;
  padding: 6px;
}

button:hover {
  background-color: rgb(88, 175, 218);
  transition: background-color 500ms ease-in-out;
}

a {
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

@media only screen and (max-width: 768px) {
  .main-container {
    width: calc(100vw - 20px);
  }
}
